<template>
  <div>
    <p>Latitude: {{ latitude }}</p>
    <p>Longitude: {{ longitude }}</p>
    <br />
    <div>
      <select
        v-model="selectedOrganization"
        @change="onOrganizationChange"
      >
        <option
          v-for="org in organizations"
          :value="org"
          :key="org.id"
        >
          {{ org.nameOfOrganization }}
        </option>
      </select>

      <button
        :disabled="!isPunchedInEnabled() || isPunchedIn"
        @click="handleCheckIn"
      >
        Punch In
      </button>
      <button
        v-if="isPunchedIn"
        :disabled="!isPunchOutEnabled() || isPunchedOut"
        @click="handlePunchOut"
      >
        Punch Out
      </button>
    </div>
    <span>
      <p>{{ userMessage }}</p>
    </span>
    <p>
      Distance to Organization: {{ distanceToOrganization.toFixed(2) }} meters
    </p>
  </div>
</template>

<script setup>
import { ref, onMounted, onUnmounted } from "vue";
import { point, distance } from "@turf/turf";

const latitude = ref(null);
const longitude = ref(null);
const userMessage = ref("");
const isPunchedIn = ref(false);
const isPunchedOut = ref(false);
const distanceToOrganization = ref(0);

const organizations = ref([
  {
    id: 1,
    nameOfOrganization: "Convenience, Dingli",
    latitude: 35.861579718553045,
    longitude: 14.384102888618935,
    meters: 20,
    startTime: "2024-02-08T11:00:00Z",
    endTime: "2024-02-08T11:03:00Z",
  },
  {
    id: 2,
    nameOfOrganization: "Barbajean",
    latitude: 35.86092166581468,
    longitude: 14.382550960199088,
    meters: 20,
    startTime: "2024-02-08T11:00:00Z",
    endTime: "2024-02-08T11:03:00Z",
  },
  {
    id: 3,
    nameOfOrganization: "Patrick Prints",
    latitude: 35.87057691018765,
    longitude: 14.446694085537114,
    meters: 20,
    startTime: "2024-02-08T11:00:00Z",
    endTime: "2024-04-08T11:03:00Z",
  },
  {
    id: 4,
    nameOfOrganization: "Master Grocer",
    latitude: 35.87154161786839,
    longitude: 14.446766513597746,
    meters: 20,
    startTime: "2024-02-08T11:00:00Z",
    endTime: "2024-04-08T11:03:00Z",
  },
]);

const selectedOrganization = ref(organizations.value[0]);
let watchId = null;

const updateLocation = (position) => {
  latitude.value = position.coords.latitude;
  longitude.value = position.coords.longitude;

  const from = point([latitude.value.toFixed(5), longitude.value.toFixed(5)]);
  const to = point([
    selectedOrganization.value.latitude.toFixed(5),
    selectedOrganization.value.longitude.toFixed(5),
  ]);
  const options = { units: "meters" };
  distanceToOrganization.value = distance(from, to, options);

  isPunchedInEnabled();
  isPunchOutEnabled();
};

const onOrganizationChange = () => {
  isPunchedIn.value = false;
  isPunchedOut.value = false;
  userMessage.value = "";

  if (latitude.value !== null && longitude.value !== null) {
    updateLocation({
      coords: {
        latitude: latitude.value,
        longitude: longitude.value,
      },
    });
  }
};

const errorCallback = (error) => {
  alert("Error Code = " + error.code + " - " + error.message);
};

const watchLocation = () => {
  if ("geolocation" in navigator) {
    watchId = navigator.geolocation.watchPosition(
      updateLocation,
      errorCallback,
      {
        enableHighAccuracy: true,
        maximumAge: 0,
      }
    );
  } else {
    alert("Geolocation is not supported by this browser.");
  }
};

const isPunchedInEnabled = () => {
  const now = new Date();
  const startTime = new Date(selectedOrganization.value.startTime);
  return (
    distanceToOrganization.value <= selectedOrganization.value.meters &&
    now >= startTime
  );
};

const isPunchOutEnabled = () => {
  const now = new Date();
  const endTime = new Date(selectedOrganization.value.endTime);
  return (
    distanceToOrganization.value <= selectedOrganization.value.meters &&
    now >= endTime
  );
};

const handleCheckIn = () => {
  userMessage.value = `Punched in at ${selectedOrganization.value.nameOfOrganization}`;
  isPunchedIn.value = true;
};

const handlePunchOut = () => {
  userMessage.value = `Punched out at ${selectedOrganization.value.nameOfOrganization}`;
  isPunchedOut.value = true;
};

onMounted(watchLocation);

onUnmounted(() => {
  if (watchId !== null) {
    navigator.geolocation.clearWatch(watchId);
  }
});
</script>
